* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

header {
  align-items: center;
  width: 100%;
  height: 60px;
  margin-bottom: 20px;
  display: flex;
  position: relative;
}

.imgLogo {
  position: absolute;
  top: 10px;
  bottom: 10px;
  left: 50px;
}

.imgLogo img {
  width: 40px;
}

nav {
  background-color: #fff;
  border: 1px solid #fff;
  justify-content: flex-end;
  align-items: center;
  height: 60px;
  padding-right: 50px;
  display: flex;
}

.nav-1200 {
  width: 1200px;
}

.nav-1024 {
  width: 1024px;
}

.listOfButtons {
  list-style: none;
  display: flex;
}

.button {
  text-transform: uppercase;
  justify-content: center;
  margin-right: 20px;
  padding-top: 22px;
  padding-bottom: 23px;
  font-size: 12px;
  font-weight: 500;
}

.listOfButtons li:last-child {
  margin-right: 0;
}

.link_a {
  color: #000;
  justify-content: center;
  align-items: center;
  height: 100%;
  padding-top: 22px;
  padding-bottom: 23px;
  text-decoration: none;
  display: flex;
}

.link_a:hover {
  color: #00acdc;
}

.link_a:active {
  color: #00acdc;
  text-decoration: underline;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
}
